<template>
    <v-app>
        <v-main>
            <div id="fixed_ratio">
                <router-view/>
            </div>
            <div id="portrait_warning">
                Rotate the device to work with the application
            </div>
        </v-main>
    </v-app>
</template>

<script>
export default {
  name: 'App',

  components: {
  },

  data: () => ({
    //
  }),
  mounted() {
      let docWidth, docHeight, docRatio, div = document.getElementById('fixed_ratio');

      onresize = function() {
          docWidth = document.documentElement.clientWidth; // ширина вьюпорта
          docHeight = document.documentElement.clientHeight; // высота вьюпорта

          docRatio = docWidth / docHeight; // соотношение сторон вьюпорта
          fullScreenProportionalElem(div, 1920, 1080); // элемент, ширина, высота
          resizeFont(div, 1920, 1080, 200); // элемент, ширина, высота, размер шрифта
      };

      function fullScreenProportionalElem(elem, width, height) {
          let ratio = width / height; // соотношение сторон элемента

          if (docRatio < ratio) {
              elem.style.width = docWidth + 'px';
              elem.style.height = Math.round(docWidth / ratio) + 'px';
              elem.style.top = Math.round(docHeight / 2 - elem.offsetHeight / 2) + 'px';
              elem.style.left = '0px';
          } else if (docRatio > ratio) {
              elem.style.width = Math.round(docHeight * ratio) + 'px';
              elem.style.height = docHeight + 'px';
              elem.style.top = '0px';
              elem.style.left = Math.round(docWidth / 2 - elem.offsetWidth / 2) + 'px';
          } else {
              elem.style.width = docWidth + 'px';
              elem.style.height = docHeight + 'px';
              elem.style.top = '0px';
              elem.style.left = '0px';
          }
      }

      function resizeFont(elem, width, height, size) {
          let ratio = width / height; // соотношение сторон элемента
          if (docRatio < ratio) elem.style.fontSize = height * size / 40000 + 'vw';
          else if (docRatio > ratio) elem.style.fontSize = width * size / 40000 + 'vh';
          // число 14062 можно менять и подстраивать под себя, будет меняться размер шрифта
      }

    onresize();
  }
};
</script>

<style lang="sass">
    body, html
        font-family: 'VT323', monospace !important
        overflow: hidden
        ::-webkit-scrollbar
            width: 0

    video
        width: 100%
        border: black 2px solid

    #fixed_ratio
        position: absolute
        //box-shadow: 0 0 3vh rgba(0,0,0,0.5)
        border-radius: 10vh
        overflow: hidden
        @media (orientation: portrait)
            visibility: hidden

    #portrait_warning
        @media (orientation: landscape)
            visibility: hidden
</style>
