<template>
    <div id="video" class="d-flex align-center justify-center"/>
</template>

<script>
    import Flashphoner from "@/libs/flashphoner";

    const SESSION_STATUS = Flashphoner.constants.SESSION_STATUS;
    const STREAM_STATUS = Flashphoner.constants.STREAM_STATUS;
    let PRELOADER_URL = null;
    const urlServer = process.env.VUE_APP_MEDIA_SERVER;
    const streamName = process.env.VUE_APP_STREAM_NAME;

    let mediaProvider = getUrlParam("mediaProvider") || null;
    let mseCutByIFrameOnly = getUrlParam("mseCutByIFrameOnly");

    function getUrlParam(name) {
        var url = window.location.href;
        // eslint-disable-next-line no-useless-escape
        name = name.replace(/[\[\]]/g, "\\$&");
        var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
            results = regex.exec(url);
        if (!results) return null;
        if (!results[2]) return '';
        return decodeURIComponent(results[2].replace(/\+/g, " "));
    }

    let Browser = {
        isIE: function () {
            return /*@cc_on!@*/false || !!document.documentMode;
        },
        isFirefox: function () {
            return typeof InstallTrigger !== 'undefined';
        },
        isChrome: function () {
            return !!window.chrome && /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor) && !/OPR/.test(navigator.userAgent);
        },
        isEdge: function () {
            return !this.isIE() && !!window.StyleMedia;
        },
        isOpera: function () {
            // eslint-disable-next-line no-undef
            return (!!window.opr && !!opr.addons) || !!window.opera || navigator.userAgent.indexOf(' OPR/') >= 0;
        },
        isiOS: function () {
            return /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
        },
        isSafari: function () {
            return /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
        },
        isAndroid: function () {
            return navigator.userAgent.toLowerCase().indexOf("android") > -1;
        },
        isSafariWebRTC: function () {
            return navigator.mediaDevices && Browser.isSafari();
        }
    };


    export default {
        name: "Stream",

        data: () => ({
            stream: null,
            session: null,
            video: null,
            publicPath: process.env.BASE_URL,
            status: null
        }),

        mounted() {
            PRELOADER_URL = `${this.publicPath}media/preloader.mp4`;
            this.video = document.getElementById('video');

            try {
                Flashphoner.init({
                    flashMediaProviderSwfLocation: `${this.publicPath}media/media-provider.swf`,
                    receiverLocation: `${this.publicPath}js/WSReceiver2.js`,
                    decoderLocation: `${this.publicPath}js/video-worker2.js`,
                    preferredMediaProvider: mediaProvider,
                    logger: {severity: "INFO"}
                });
            } catch(e) {
                console.log("Your browser doesn't support Flash or WebRTC technology");
            }

            if (Flashphoner.getMediaProviders()[0] === "WSPlayer") {
                Flashphoner.playFirstSound();
            } else if (Browser.isSafariWebRTC() || Flashphoner.getMediaProviders()[0] === "MSE") {
                Flashphoner.playFirstVideo(this.video, false, PRELOADER_URL)
                    .then(() => {
                        this.start();
                    })
                    .catch(() => {
                        console.log("Can't play preloader video");
                    });
                return;
            }

            this.start();
        },

        beforeDestroy() {
          if (this.stream) {
              this.stream.stop()
          }
        },

        methods: {
            start() {
                //check if we already have session
                if (Flashphoner.getSessions().length > 0) {
                    this.session = Flashphoner.getSessions()[0];
                    if (this.session.getServerUrl() === urlServer) {
                        this.playStream(this.session);
                        return;
                    } else {
                        //remove session DISCONNECTED and FAILED callbacks
                        this.session.on(SESSION_STATUS.DISCONNECTED, () => {});
                        this.session.on(SESSION_STATUS.FAILED, () => {});
                        this.session.disconnect();
                    }
                }

                //create session
                console.log("Create new session with url " + urlServer);
                Flashphoner.createSession({
                    urlServer
                })
                    .on(SESSION_STATUS.ESTABLISHED, (session) => {
                        //session connected, start playback
                        this.session = session;
                        this.status = session.status();
                        this.playStream();
                    })
                    .on(SESSION_STATUS.DISCONNECTED, () => {
                        this.status = "DISCONNECTED";
                    })
                    .on(SESSION_STATUS.FAILED, () => {
                        this.status = "FAILED";
                    });
            },

            playStream() {
                let options = {
                    name: streamName,
                    display: this.video,
                };
                if (Flashphoner.getMediaProviders()[0] === "MSE" && mseCutByIFrameOnly) {
                    options.mediaConnectionConstraints = {
                        cutByIFrameOnly: mseCutByIFrameOnly
                    }
                }
                this.stream = this.session.createStream(options)
                    .on(STREAM_STATUS.PLAYING, () => {
                        this.stream.muteRemoteAudio();
                        this.status = "PLAYING";
                    })
                    .on(STREAM_STATUS.STOPPED, () => {
                        this.status = "STOPPED";
                    })
                    .on(STREAM_STATUS.FAILED, () => {
                        this.status = "FAILED";
                    })
                    .on(STREAM_STATUS.NOT_ENOUGH_BANDWIDTH, () => {
                        this.status = "NOT_ENOUGH_BANDWIDTH";
                    });
                this.stream.play();
            }
        },
        watch: {
            status() {
                this.$emit('input', this.status)
            }
        }
    }
</script>

<style scoped lang="sass">
    #video
        width: 95%
        height: 95%
</style>
