  <div class="main" :class="{'fullscreen': fullscreenState}">
    <div class="left shadow d-flex flex-column align-center justify-center">
      <v-dialog
              v-model="lightDialog"
              width="350"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
                  :disabled="overlay"
