                  v-on="on"
          >
            <v-icon
                    size="10vh"
                    dark
            >
              mdi-lightbulb-on-outline
            </v-icon>
          </v-btn>
        </template>

        <v-card>
          <v-card-title class="" :style="{ background: `rgb(${red}, ${green}, ${blue})`, textShadow: '1px 1px 1px white'}">
            Light control
            <v-spacer></v-spacer>
            <v-btn @click="lightDialog = false" icon>
              <v-icon :style="{ textShadow: '1px 1px 1px white'}">mdi-close</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text>
            <br>
            <v-slider
                    v-model="red"
                    :max="255"
                    label="R"
                    color="red"
                    track-color="grey"
                    class="align-center"
                    thumb-label
            >
            </v-slider>
            <v-slider
                    v-model="green"
                    :max="255"
                    label="G"
                    color="green"
                    track-color="grey"
                    class="align-center"
                    thumb-label
            >
            </v-slider>
            <v-slider
                    v-model="blue"
                    :max="255"
                    label="B"
                    color="blue"
                    track-color="grey"
                    class="align-center"
                    thumb-label
            >
            </v-slider>
            <v-switch
                    @click="changeLampState"
                    inset
                    color="grey darken-2"
                    v-model="lamp"
                    :label="'Lamp'"
            ></v-switch>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-btn
              @click="pressKey('forward', true)"
              :disabled="disableButtons || overlay"
              class="buttons_margin"
              fab dark large color="black"
              height="15%"
              width="56.25%"
      >
        <v-icon
                size="12vh"
                dark
        >
          mdi-chevron-up
        </v-icon>
      </v-btn>
      <v-btn
              @click="pressKey('gun', true)"
              :disabled="true"
              class="buttons_margin"
              fab dark large color="black"
              height="15%"
              width="56.25%"
      >
        <v-icon
                size="8vh"
                dark
        >
          mdi-flare
        </v-icon>
      </v-btn>
    </div>
    <div class="center d-flex align-center justify-center">
      <div v-if="!overlay" class="score_container d-flex flex-column justify-end">
        <div class="score d-flex justify-end align-center">
          <img src="../assets/light.png" height="47%"><span>{{ score }}/3</span>
        </div>
      </div>

      <div v-if="overlay" class="screen_overlay">
        <div v-if="overlay === 'win'" class="d-flex flex-column justify-center align-center">
          <img src="../assets/win.png" height="30%">
          <span class="win">You are win!</span>
          <span class="button" @click="restart">
            restart
          </span>
        </div>

        <div v-if="overlay === 'start'" class="start d-flex flex-column justify-center align-center">
          <span class="company">Escape Room Doctor</span>
          <img src="../assets/erd.png" height="30%">
          <span>Spider Game</span>
          <span>
            <span class="button" @click="play">play</span>
            <span class="button" @click="watch">watch</span>
          </span>
        </div>

        <div v-if="overlay === 'loading'" class="d-flex flex-column justify-center align-center">
          Loading...
        </div>
      </div>

      <Stream v-if="stream" v-model="streamStatus"/>
    </div>
    <div class="right shadow d-flex flex-column align-center justify-center">
      <v-btn
              @click="toggleFullscreen"
              class="buttons_margin"
              fab dark large color="black"
              height="15%"
              width="56.25%"
      >
        <v-icon
                size="10vh"
                dark
        >
          {{ fullscreenState ? 'mdi-fullscreen-exit' : 'mdi-fullscreen' }}
        </v-icon>
      </v-btn>
      <v-btn
              @click="pressKey('right', true)"
              :disabled="disableButtons || overlay"
              class="buttons_margin"
              fab dark large color="black"
              height="15%"
              width="56.25%"
      >
        <v-icon
                size="10vh"
                dark
        >
          mdi-rotate-right
        </v-icon>
      </v-btn>
      <v-btn
              @click="pressKey('left', true)"
              :disabled="disableButtons || overlay"
              class="buttons_margin"
              fab dark large color="black"
              height="15%"
              width="56.25%"
      >
        <v-icon
                size="10vh"
                dark
        >
          mdi-rotate-left
        </v-icon>
      </v-btn>
