<script>
import QueenBridge from '@/libs/queenbridge';
import Stream from "@/components/Stream";

let qb;
const delay = ms => new Promise(resolve => setTimeout(resolve, ms));

export default {
  name: 'Home',
  components: {
    Stream
  },

  data: () => ({
    lightDialog: false,
    red: 0,
    green: 0,
    blue: 0,
    lamp: false,
    score: 0,
    overlay: 'start',
    isPlaying: false,
    stream: false,
    streamStatus: null,
    fullscreenState: false,
    disableButtons: true
  }),

  mounted() {
    document.addEventListener('keydown', (event) => {
      const keyName = event.key;
      // console.log(keyName);
      switch (keyName) {
        case 'ArrowUp':     this.pressKey('forward', true, true); break;
        case 'ArrowLeft':   this.pressKey('left', true, true); break;
        case 'ArrowRight':  this.pressKey('right', true, true); break;
        case 'f':           this.pressKey('gun', true, true); break;
      }
    }, false);

    document.addEventListener('keyup', (event) => {
      const keyName = event.key;
      // console.log(keyName);
      switch (keyName) {
        case 'ArrowUp':     this.pressKey('forward', false); break;
        case 'ArrowLeft':   this.pressKey('left', false); break;
        case 'ArrowRight':  this.pressKey('right', false); break;
        case 'f':           this.pressKey('gun', false); break;
        case 'F11':         event.preventDefault(); this.toggleFullscreen(); break;
      }
    }, false);

    qb = new QueenBridge(process.env.VUE_APP_QUEEN_BRIDGE, {
      keepOffline: 10000,
      override: true
    });

    qb.on('connect', () => {
      console.log('connected to Queen Bridge on ' + process.env.VUE_APP_QUEEN_BRIDGE);
      setTimeout(() => {
        qb.subscribe('rooms/spider_attack');
      }, 1500);
    });
    qb.on('disconnect', () => {
      console.log('disconnected from Queen Bridge');
    });
    qb.on('receive', (data) => {
      console.log('[Queen Bridge]: ' + JSON.stringify(data.payload));
      if (data.srcId === 'spider_attack' && data.payload.type === 'trigger') {
        for (let object of data.payload.data.objects) {
          if (object.type === 'out' && object.name === 'light') {
            this.lamp = object.state === '1';
          }
          else if (object.type === 'uni' && object.name === 'score') {
            this.score = object.state;
            if (this.score == 3) {
              (async () => {
                await delay(5000);
                this.overlay = 'win';
                this.disableButtons = true;
              })().catch(err => {
                console.error(err);
              });
            }
            else this.overlay = null
          }
        }
      }
    });

    document.onfullscreenchange = () => {
      this.changeFullScreenState()
    }
  },

  methods: {
    changeLampState() {
      qb.send("spider_attack", `set out.light.state ${this.lamp}`);
    },

    async pressKey(key, state, keyboard = false) {
      qb.send("spider_attack", `set out.${key}.state ${state}`);
      console.log(`set ${key} ${state}`);
      if (!keyboard) {
        await delay(1000);
        if (state) qb.send("spider_attack", `set out.${key}.state false`);
      }
    },

    toggleFullscreen() {
      let doc = window.document;
      let docEl = doc.documentElement;

      let requestFullScreen = docEl.requestFullscreen || docEl.mozRequestFullScreen || docEl.webkitRequestFullScreen || docEl.msRequestFullscreen;
      let cancelFullScreen = doc.exitFullscreen || doc.mozCancelFullScreen || doc.webkitExitFullscreen || doc.msExitFullscreen;

      if(!doc.fullscreenElement && !doc.mozFullScreenElement && !doc.webkitFullscreenElement && !doc.msFullscreenElement) {
        requestFullScreen.call(docEl);
      }
      else {
        cancelFullScreen.call(doc);
      }
    },

    changeFullScreenState() {
      let doc = window.document;
      this.fullscreenState = !(!doc.fullscreenElement && !doc.mozFullScreenElement && !doc.webkitFullscreenElement && !doc.msFullscreenElement);
    },

    play() {
      this.stream = true;
      this.overlay = 'loading';
      this.disableButtons = false;
      qb.send("spider_attack", `setmode idle`);
      qb.send("spider_attack", `setmode tune`);
      qb.send("spider_attack", `setmode playing`);
    },

    watch () {
      this.stream = true;
      this.overlay = 'loading';
      this.disableButtons = true;
    },

    restart() {
      this.stream = false;
      this.overlay = 'start';
      this.disableButtons = true;
    }
  },
  watch: {
    red() {
      qb.send("spider_attack", `set pwm.red.state ${this.red}`);
    },
    green() {
      qb.send("spider_attack", `set pwm.green.state ${this.green}`);
    },
    blue() {
      qb.send("spider_attack", `set pwm.blue.state ${this.blue}`);
    },
    streamStatus() {
      if (this.streamStatus === 'PLAYING') {
        (async () => {
          await delay(1000);
          this.overlay = null
        })().catch(err => {
          console.error(err);
        });
      }
    }
  }
}
</script>

